import React from 'react';
import { Typeahead, Highlighter } from 'react-bootstrap-typeahead';
import 'react-bootstrap-typeahead';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { changeRoot } from '../state/actions/TreeActions';
import { showPerson } from '../state/actions/DetailsActions';
import { personType } from '../datasource/DataTypes';

export class Search extends React.Component {
    renderYear = year => {
        return year ? year : '?';
    };

    renderMenuItemChildren = (options, props, index) => {
        return [
            <Highlighter key="name" search={props.text}>
                {options.name}
            </Highlighter>,
            <small key="lifespan">
                &nbsp;({this.renderYear(options.yearOfBirth)} -{' '}
                {this.renderYear(options.yearOfDeath)})
            </small>
        ];
    };

    onChange = selection => {
        if (selection[0]) {
            this.props.showPerson(selection[0].id);
            this.props.changeRoot(selection[0].id);
        }
    };

    render() {
        return (
            <div>
                <Typeahead
                    id="searchAncestors"
                    options={this.props.options}
                    placeholder="Start typing ancestor name..."
                    minLength={3}
                    selectHintOnEnter={true}
                    renderMenuItemChildren={this.renderMenuItemChildren}
                    labelKey="name"
                    onChange={this.onChange}
                />
            </div>
        );
    }
}

Search.propTypes = {
    changeRoot: PropTypes.func.isRequired,
    showPerson: PropTypes.func.isRequired,
    options: PropTypes.arrayOf(personType)
};

const mapStateToProps = state => ({
    options: state.search.options
});

export default connect(mapStateToProps, { changeRoot, showPerson })(Search);
