import React from 'react';
import { Col, Container, Row, UncontrolledTooltip } from 'reactstrap';
import Person from './Person';
import Gallery from './Gallery';
import {
    showPerson,
    selectResourcePreview,
    toggleGallery
} from '../state/actions/DetailsActions';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { personType, factType } from '../datasource/DataTypes';

export class Details extends React.Component {
    renderFacts = () => {
        if (this.props.facts) {
            return this.props.facts.map((fact, factIndex) => (
                <Row
                    className="fact"
                    onClick={() => this.props.selectResourcePreview(factIndex)}
                    key={factIndex}
                >
                    <Col xs={1} className="factYear">
                        {fact.year}
                    </Col>
                    <Col xs={11} className="factDetail">
                        {fact.fact}
                    </Col>
                </Row>
            ));
        }
    };

    renderPreview = () => {
        return (
            <Row>
                <Col xs={12}>
                    <img
                        src={this.props.currentResource}
                        alt={this.props.currentFact}
                        className="previewBox"
                        id="previewBox"
                        onClick={this.props.toggleGallery}
                    />
                    <UncontrolledTooltip placement="auto" target="previewBox">
                        {this.props.currentFact}
                    </UncontrolledTooltip>
                    {this.props.showGallery && <Gallery />}
                </Col>
            </Row>
        );
    };

    render() {
        return (
            <div className="detailsHolder">
                {this.props.person && (
                    <div className="details">
                        <div>
                            <button
                                className="fab closeDetails"
                                onClick={() => this.props.showPerson()}
                            >
                                &times;
                            </button>
                        </div>
                        <Container fluid={true}>
                            <Row id="personId">
                                <Col xs={12} className="h3 text-center">
                                    <Person person={this.props.person} />
                                </Col>
                            </Row>
                            <UncontrolledTooltip
                                placement="auto"
                                target="personId"
                            >
                                {this.props.personId}
                            </UncontrolledTooltip>
                            {this.props.currentResource && this.renderPreview()}
                        </Container>
                        <Container fluid={true} className="detailsFacts">
                            {this.renderFacts()}
                        </Container>
                    </div>
                )}
            </div>
        );
    }
}

Details.propTypes = {
    personId: PropTypes.number,
    person: personType,
    facts: PropTypes.arrayOf(factType),
    currentResource: PropTypes.string,
    currentFact: PropTypes.string,
    showGallery: PropTypes.bool,
    galleryItems: PropTypes.arrayOf(
        PropTypes.shape({
            src: PropTypes.string,
            caption: PropTypes.string
        })
    ),
    showPerson: PropTypes.func.isRequired,
    selectResourcePreview: PropTypes.func.isRequired,
    toggleGallery: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
    personId: state.details.personId,
    person: state.details.person,
    facts: state.details.facts,
    currentResource: state.details.currentResource,
    currentFact: state.details.currentFact,
    showGallery: state.details.showGallery,
    galleryItems: state.details.galleryItems
});

export default connect(mapStateToProps, {
    showPerson,
    selectResourcePreview,
    toggleGallery
})(Details);
