import React from 'react';
import * as d3 from 'd3';
import { connect } from 'react-redux';
import { Col, Container, Row } from 'reactstrap';
import ScrollWindow from './ScrollWindow';
import AncestryCoverage from './charts/AncestryCoverage';
import NameFrequency from './charts/NameFrequency';
import OriginMap from './charts/OriginMap';
import DataAnalysis, { PersonTreeType } from './charts/DataAnalysis';

export class AnalysisView extends React.Component {
    componentDidMount() {
        this.colourMap = this.colourMap();
        this.nameFrequency = new NameFrequency(
            this.nameFrequencyChart,
            this.colourMap,
            name => {
                this.ancestryCoverage.selectName(name);
                this.originMap.redrawOriginChart();
            }
        );

        this.originMap = new OriginMap(
            this.originMapChart,
            this.props.places,
            place => {
                this.ancestryCoverage.selectPlace(place);
                this.nameFrequency.redrawNameChart();
            }
        );

        this.ancestryCoverage = new AncestryCoverage(
            this.ancestryCoverageChart,
            this.colourMap,
            data => {
                this.nameFrequency.redrawNameChart(data);
                this.originMap.redrawOriginChart(data);
            }
        );

        this.ancestryCoverage.drawAncestorChart(this.props.data);
        this.nameFrequency.drawNameChart(this.props.data);
        this.originMap.drawOriginChart(this.props.data);
    }

    colourMap() {
        return d3.scaleOrdinal(d3.quantize(d3.interpolateRainbow, 2 ^ 30));
    }

    render() {
        const ancestryCoverageChart = el => (this.ancestryCoverageChart = el);
        const nameFrequencyChart = el => (this.nameFrequencyChart = el);
        const originMapChart = el => (this.originMapChart = el);
        return (
            <span>
                <ScrollWindow>
                    <Container fluid={true}>
                        <Row>
                            <Col sm={6} className="text-center">
                                <Row>
                                    <Col sm={12} className="text-center">
                                        <h1>Ancestry Coverage</h1>
                                    </Col>
                                    <Col sm={12} className="text-center">
                                        <div
                                            ref={ancestryCoverageChart}
                                            className="ancestryCoverageChart"
                                        />
                                    </Col>
                                    <Col sm={12} className="text-center">
                                        <h1>Name Frequency</h1>
                                    </Col>
                                    <Col sm={12} className="text-center">
                                        <div
                                            ref={nameFrequencyChart}
                                            className="nameFrequencyChart"
                                        />
                                    </Col>
                                </Row>
                            </Col>
                            <Col sm={6} className="text-center">
                                <Row>
                                    <Col sm={12} className="text-center">
                                        <h1>Origin Map</h1>
                                    </Col>
                                    <Col sm={12} className="text-center">
                                        <div
                                            ref={originMapChart}
                                            className="originMapChart"
                                        />
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Container>
                </ScrollWindow>
            </span>
        );
    }
}

AnalysisView.propTypes = {
    data: PersonTreeType
};

const mapStateToProps = state => ({
    data: DataAnalysis.getData(state, state.tree.rootId),
    places: state.dataRepository.places
});

export default connect(mapStateToProps)(AnalysisView);
