import {
    SET_DISPLAYED_DETAILS,
    SET_RESOURCE_PREVIEW,
    GALLERY_TOGGLED,
    SET_GALLERY_INDEX,
    SET_GALLERY_SCALE
} from '../actions/Types';

const initialState = {
    personId: null,
    person: null,
    facts: null,
    currentResource: null,
    currentFact: null,
    showGallery: false,
    galleryItems: null,
    galleryIndex: null,
    galleryScale: 0
};

export default function(state = initialState, action) {
    switch (action.type) {
        case SET_DISPLAYED_DETAILS:
            return {
                ...state,
                personId: action.data.personId,
                person: action.data.person,
                facts: action.data.facts,
                currentResource: action.data.currentResource,
                currentFact: action.data.currentFact,
                showGallery: false,
                galleryItems: action.data.galleryItems
            };
        case SET_RESOURCE_PREVIEW:
            return {
                ...state,
                currentResource: action.data.resource,
                currentFact: action.data.fact
            };
        case GALLERY_TOGGLED:
            return {
                ...state,
                showGallery: !state.showGallery,
                galleryIndex: action.data.index
            };
        case SET_GALLERY_INDEX:
            return {
                ...state,
                galleryIndex: action.data.index,
                galleryScale: 1
            };
        case SET_GALLERY_SCALE:
            return {
                ...state,
                galleryScale: action.data.scale
            };
        default:
            return state;
    }
}
