import {
    setDisplayedDetails,
    setResourcePreview,
    galleryToggled,
    setGalleryIndex,
    setGalleryScale
} from './Types';
import datasource from '../../datasource/Datasource';

const MAX_SCALE_SIZE = 5;

const getFirstResource = facts => {
    if (facts && facts.length > 0) {
        for (let index = 0; index < facts.length; index++) {
            if (facts[index].url && facts[index].url.length > 0) {
                return index;
            }
        }
    }
    return -1;
};

const getResourceUrl = url => {
    if (url && url.length > 0) {
        return `resources/${url}`;
    }
    return 'images/no-image-available-icon-6.gif';
};

const getGalleryItems = facts => {
    let items = null;

    if (facts && facts.length > 0) {
        items = [];
        for (let fact of facts) {
            if (fact.url && fact.url.length > 0) {
                items.push({
                    src: getResourceUrl(fact.url),
                    caption: fact.fact
                });
            }
        }
    }

    return items;
};

const currentGalleryIndex = (currentResource, items) => {
    let index = 0;
    for (let item of items) {
        if (item.src === currentResource) {
            return index;
        }
        index++;
    }
    return 0;
};

export const showPerson = id => (dispatch, getState) => {
    let person = datasource.findPerson(getState(), id);
    let facts = datasource.findFacts(getState(), id);
    let firstResource = getFirstResource(facts);
    let currentResource = null;
    let currentFact = null;
    let galleryItems = getGalleryItems(facts);

    if (firstResource > -1) {
        currentResource = getResourceUrl(facts[firstResource].url);
        currentFact = facts[firstResource].fact;
    }

    dispatch(
        setDisplayedDetails(
            id,
            person,
            facts,
            currentResource,
            currentFact,
            galleryItems
        )
    );
};

export const selectResourcePreview = factIndex => (dispatch, getState) => {
    const facts = getState().details.facts;
    let resource = getResourceUrl(facts[factIndex].url);
    let fact = facts[factIndex].fact;

    dispatch(setResourcePreview(resource, fact));
};

export const toggleGallery = () => (dispatch, getState) => {
    const detailsState = getState().details;
    const galleyShowing = detailsState.showGallery;
    let galleryIndex = null;
    if (!galleyShowing) {
        galleryIndex = currentGalleryIndex(
            detailsState.currentResource,
            detailsState.galleryItems
        );
    }
    dispatch(galleryToggled(galleryIndex));
};

export const selectGalleryNext = () => (dispatch, getState) => {
    const detailsState = getState().details;
    let index =
        detailsState.galleryIndex === detailsState.galleryItems.length - 1
            ? 0
            : detailsState.galleryIndex + 1;

    dispatch(setGalleryIndex(index));
};

export const selectGalleryPrev = () => (dispatch, getState) => {
    const detailsState = getState().details;
    let index =
        detailsState.galleryIndex === 0
            ? detailsState.galleryItems.length - 1
            : detailsState.galleryIndex - 1;

    dispatch(setGalleryIndex(index));
};

export const selectGalleryIndex = index => dispatch => {
    dispatch(setGalleryIndex(index));
};

export const selectGalleryScalePlus = scale => (dispatch, getState) => {
    const detailsState = getState().details;
    let scale =
        detailsState.galleryScale === MAX_SCALE_SIZE
            ? 0
            : detailsState.galleryScale + 1;

    dispatch(setGalleryScale(scale));
};

export const selectGalleryScaleMinus = scale => (dispatch, getState) => {
    const detailsState = getState().details;
    let scale =
        detailsState.galleryScale === 0
            ? MAX_SCALE_SIZE
            : detailsState.galleryScale - 1;

    dispatch(setGalleryScale(scale));
};
