/*eslint no-restricted-globals: 0*/
import React from 'react';
import PropTypes from 'prop-types';

export default class ScrollWindow extends React.Component {
    constructor(props) {
        super(props);
        this.privateState = {
            isMouseDown: false,
            lastMousePositionX: null,
            lastMousePositionY: null,
            startTime: null
        };
        this.refElement = null;
        this.clearListeners = [];

        this.scrollAttrX = 'scrollLeft';
        this.eventMoveX = 'clientX';
        this.scrollLengthW = 'scrollWidth';
        this.scrollOffsetW = 'offsetWidth';

        this.scrollAttrY = 'scrollTop';
        this.eventMoveY = 'clientY';
        this.scrollLengthH = 'scrollHeight';
        this.scrollOffsetH = 'offsetHeight';
    }

    setPrivateState(state) {
        this.privateState = { ...this.privateState, ...state };
    }

    addEventListenerWithClear(type, func) {
        this.refElement.addEventListener(type, func);
        const clear = this.removeListenerFactory(type, func);
        this.clearListeners.push(clear);
    }

    removeListenerFactory(eventName, listener) {
        return () => removeEventListener(eventName, listener);
    }

    componentDidMount() {
        this.addEventListenerWithClear('mouseup', this.onMouseUp);
        this.addEventListenerWithClear('mouseleave', this.onMouseUp);
        this.addEventListenerWithClear('mousemove', this.onMouseMove);
    }

    componentWillUnmount() {
        this.clearListeners.forEach(clear => clear());
    }

    scroll(accelerationX, accelerationY) {
        const maxW =
            this.refElement[this.scrollLengthW] -
            this.refElement[this.scrollOffsetW];
        const maxH =
            this.refElement[this.scrollLengthH] -
            this.refElement[this.scrollOffsetH];

        this.animation = setTimeout(() => {
            scrollTo(this.scrollAttrX, maxW, accelerationX, true);
            scrollTo(this.scrollAttrY, maxH, accelerationY, false);
        }, 10);
    }

    scrollTo = (scrollAttr, max, acceleration, isX) => {
        const scroll = this.refElement[scrollAttr];
        if (scroll !== 0 && scroll !== max && acceleration !== 0) {
            this.refElement[this.scrollAttr] += acceleration;
            acceleration =
                acceleration > 0 ? acceleration - 1 : acceleration + 1;
            if (isX) {
                this.scroll(acceleration, 0);
            } else {
                this.scroll(0, acceleration);
            }
        }
    };

    onMouseMove = event => {
        const {
            isMouseDown,
            lastMousePositionX,
            lastMousePositionY
        } = this.privateState;
        if (!isMouseDown) {
            return null;
        }

        if (
            this.refElement === null ||
            lastMousePositionX === null ||
            lastMousePositionY === null
        ) {
            return null;
        }

        this.refElement[this.scrollAttrX] +=
            lastMousePositionX - event[this.eventMoveX];

        this.refElement[this.scrollAttrY] +=
            lastMousePositionY - event[this.eventMoveY];

        this.setPrivateState({
            lastMousePositionX: event[this.eventMoveX],
            lastMousePositionY: event[this.eventMoveY]
        });
    };

    onMouseUp = () => {
        const { startTime, positionStartX, positionStartY } = this.privateState;
        const time = (new Date() - startTime) / 1000;

        const positionEndX = this.refElement[this.scrollAttrX];
        const distanceX = positionEndX - positionStartX;
        const velocityX = Math.round(distanceX / time);
        const accelerationX = Math.round(velocityX / time / 100);

        const positionEndY = this.refElement[this.scrollAttrY];
        const distanceY = positionEndY - positionStartY;
        const velocityY = Math.round(distanceY / time);
        const accelerationY = Math.round(velocityY / time / 100);

        this.setPrivateState({
            isMouseDown: false,
            lastMousePositionX: null,
            lastMousePositionY: null,
            time
        });
        this.scroll(accelerationX, accelerationY);
    };

    provisionOnMouseDown = event => {
        if (this.animation) {
            clearTimeout(this.animation);
        }
        this.setPrivateState({
            isMouseDown: true,
            lastMousePositionX: event[this.eventMoveX],
            lastMousePositionY: event[this.eventMoveY],
            startTime: new Date(),
            positionStartX: this.refElement[this.scrollAttrX],
            positionStartY: this.refElement[this.scrollAttrY]
        });
    };

    provisionRef = element => {
        this.refElement = element;
    };

    render() {
        return (
            <div
                className={`scrollable ${this.props.className}`}
                ref={this.provisionRef}
                onMouseDown={this.provisionOnMouseDown}
            >
                {React.Children.map(this.props.children, child =>
                    React.Children.only(child)
                )}
            </div>
        );
    }
}

ScrollWindow.defaultProps = {
    className: ''
};

ScrollWindow.propTypes = {
    className: PropTypes.string
};
