import React from 'react';
import { Col, Container, Row } from 'reactstrap';
import Person from './Person';
import Siblings from './Siblings';
import datasource from '../datasource/Datasource';
import { personType } from '../datasource/DataTypes';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { showPerson } from '../state/actions/DetailsActions';
import SubTreeNode from './TreeNode';

export class TreeNode extends React.Component {
    selectPerson = () => {
        this.props.showPerson(this.props.id);
    };

    render() {
        return (
            <div>
                <Container className="node">
                    <Row className="nodePerson">
                        <Col
                            sm={6}
                            className="vcenter showOption"
                            onClick={this.selectPerson}
                        >
                            <Person
                                person={this.props.person}
                                divId={`personCol${this.props.id}`}
                            />
                        </Col>
                        {this.props.person && (
                            <Siblings personId={this.props.id} />
                        )}
                        <Col sm={6}>
                            {this.props.person && (
                                <Container className="node nodeParents">
                                    <Row className="father">
                                        <Col sm={12} className="nodeFather">
                                            <SubTreeNode
                                                id={this.props.person.father}
                                            />
                                        </Col>
                                    </Row>
                                    <Row className="mother">
                                        <Col sm={12} className="nodeMother">
                                            <SubTreeNode
                                                id={this.props.person.mother}
                                            />
                                        </Col>
                                    </Row>
                                </Container>
                            )}
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

TreeNode.propTypes = {
    showPerson: PropTypes.func,
    id: PropTypes.number,
    person: personType
};

const mapStateToProps = (state, ownProps) => ({
    person: datasource.findPerson(state, ownProps.id)
});

export default connect(mapStateToProps, { showPerson })(TreeNode);
