import React from 'react';
import {
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
    CarouselCaption,
    Modal,
    ModalBody
} from 'reactstrap';
import ScrollWindow from './ScrollWindow';
import PropTypes from 'prop-types';
import {
    selectGalleryNext,
    selectGalleryPrev,
    selectGalleryScalePlus,
    selectGalleryScaleMinus,
    selectGalleryIndex,
    toggleGallery
} from '../state/actions/DetailsActions';
import { connect } from 'react-redux';

export class Gallery extends React.Component {
    carouselItems = () => {
        return this.props.galleryItems.map((item, index) => (
            <CarouselItem key={index}>
                <ScrollWindow className="galleryItem">
                    <div className={`galleryImageX${this.props.galleryScale}`}>
                        <img
                            src={item.src}
                            alt={item.caption}
                            className="galleryImageX1"
                            draggable={false}
                        />
                    </div>
                </ScrollWindow>
                <CarouselCaption captionText={item.caption} />
            </CarouselItem>
        ));
    };

    renderButtons = () => {
        return (
            <div>
                <button
                    className="fab closeGallery"
                    onClick={this.props.toggleGallery}
                >
                    &times;
                </button>
                <button
                    className="fab plusGallery"
                    onClick={this.props.selectGalleryScalePlus}
                >
                    &#43;
                </button>
                <button
                    className="fab minusGallery"
                    onClick={this.props.selectGalleryScaleMinus}
                >
                    &minus;
                </button>
            </div>
        );
    };

    render() {
        return (
            <Modal
                isOpen={this.props.galleryItems.length > 0}
                external={this.renderButtons()}
                className="galleryModal"
                contentClassName="galleryContent"
            >
                <ModalBody>
                    <Carousel
                        activeIndex={this.props.galleryIndex}
                        next={this.props.selectGalleryNext}
                        previous={this.props.selectGalleryPrev}
                        interval={false}
                    >
                        <CarouselIndicators
                            items={this.props.galleryItems}
                            activeIndex={this.props.galleryIndex}
                            onClickHandler={this.props.selectGalleryIndex}
                        />
                        {this.carouselItems()}
                        <CarouselControl
                            direction="prev"
                            directionText="Previous"
                            onClickHandler={this.props.selectGalleryPrev}
                        />
                        <CarouselControl
                            direction="next"
                            directionText="Next"
                            onClickHandler={this.props.selectGalleryNext}
                        />
                    </Carousel>
                </ModalBody>
            </Modal>
        );
    }
}

Gallery.propTypes = {
    galleryItems: PropTypes.arrayOf(
        PropTypes.shape({
            src: PropTypes.string,
            caption: PropTypes.string
        })
    ).isRequired,
    galleryIndex: PropTypes.number.isRequired,
    galleryScale: PropTypes.number.isRequired,
    toggleGallery: PropTypes.func.isRequired,
    selectGalleryNext: PropTypes.func.isRequired,
    selectGalleryPrev: PropTypes.func.isRequired,
    selectGalleryScalePlus: PropTypes.func.isRequired,
    selectGalleryScaleMinus: PropTypes.func.isRequired,
    selectGalleryIndex: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
    galleryItems: state.details.galleryItems,
    galleryIndex: state.details.galleryIndex,
    galleryScale: state.details.galleryScale
});

export default connect(mapStateToProps, {
    selectGalleryNext,
    selectGalleryPrev,
    selectGalleryScalePlus,
    selectGalleryScaleMinus,
    selectGalleryIndex,
    toggleGallery
})(Gallery);
