export const DATA_LOADED = 'DATA_LOADED';
export const SET_ROOT = 'SET_ROOT';
export const SET_VIEW = 'SET_VIEW';
export const SET_DISPLAYED_DETAILS = 'SET_DISPLAYED_DETAILS';
export const SET_RESOURCE_PREVIEW = 'SET_RESOURCE_PREVIEW';
export const GALLERY_TOGGLED = 'GALLERY_TOGGLED';
export const SET_GALLERY_INDEX = 'SET_GALLERY_INDEX';
export const SET_GALLERY_SCALE = 'SET_GALLERY_SCALE';
export const SEARCH_OPTIONS = 'SEARCH_OPTIONS';

export const dataLoaded = (people, facts, places) => ({
    type: DATA_LOADED,
    data: {
        people: people,
        facts: facts,
        places: places
    }
});

export const setRoot = rootId => ({
    type: SET_ROOT,
    data: {
        rootId: rootId
    }
});

export const setView = view => ({
    type: SET_VIEW,
    data: {
        view: view
    }
});

export const setDisplayedDetails = (
    id,
    person,
    facts,
    currentResource,
    currentFact,
    galleryItems
) => ({
    type: SET_DISPLAYED_DETAILS,
    data: {
        personId: id,
        person: person,
        facts: facts,
        currentResource: currentResource,
        currentFact: currentFact,
        galleryItems: galleryItems
    }
});

export const setResourcePreview = (resource, fact) => ({
    type: SET_RESOURCE_PREVIEW,
    data: {
        resource: resource,
        fact: fact
    }
});

export const galleryToggled = index => ({
    type: GALLERY_TOGGLED,
    data: {
        index: index
    }
});

export const setGalleryIndex = index => ({
    type: SET_GALLERY_INDEX,
    data: {
        index: index
    }
});

export const setGalleryScale = scale => ({
    type: SET_GALLERY_SCALE,
    data: {
        scale: scale
    }
});

export const searchOptions = options => ({
    type: SEARCH_OPTIONS,
    data: {
        options: options
    }
});
