import datasource from '../../datasource/Datasource';
import PropTypes from 'prop-types';

export const PersonTreeType = PropTypes.shape({
    name: PropTypes.string,
    surname: PropTypes.string,
    yearOfBirth: PropTypes.number,
    yearOfDeath: PropTypes.number,
    id: PropTypes.number,
    children: PropTypes.array,
    mother: PropTypes.boolean
});

export default class DataAnalysis {
    static getData(state, id, mother) {
        const person = datasource.findPerson(state, id);
        let surname = person.name.split(' ')[person.name.split(' ').length - 1];
        surname = surname === '?' || surname === '' ? '<Unknown>' : surname;
        const personTreeData = {
            name: person.name,
            surname: surname,
            yearOfBirth: person.yearOfBirth,
            yearOfDeath: person.yearOfDeath,
            id: person.id,
            mother: mother,
            origin: person.origin
        };

        const parentData = DataAnalysis.getParentData(state, person);

        if (parentData.length > 0) {
            personTreeData.children = parentData;
        }
        return personTreeData;
    }

    static getParentData(state, person) {
        const parentData = [];
        if (person.father) {
            const father = DataAnalysis.getData(state, person.father);
            parentData.push(father);
        }
        if (person.mother) {
            const mother = DataAnalysis.getData(state, person.mother, true);
            parentData.push(mother);
        }
        return parentData;
    }

    static extractNameData(data) {
        const names = [];
        DataAnalysis.populateNames(data, names);

        const nameFrequency = DataAnalysis.calcFrequency(names);
        return DataAnalysis.sortDataTable(
            DataAnalysis.createDataTable(nameFrequency)
        );
    }

    static createDataTable(nameFrequency) {
        return Object.entries(nameFrequency).reduce((acc, curr) => {
            acc.push({ name: curr[0], quantity: curr[1] });
            return acc;
        }, []);
    }

    static sortDataTable(namesData) {
        namesData.sort((a, b) => {
            return b.quantity - a.quantity;
        });
        return namesData;
    }

    static calcFrequency(names) {
        return names.reduce((acc, name) => {
            acc[name] ? acc[name]++ : (acc[name] = 1);
            return acc;
        }, {});
    }

    static populateNames(person, names) {
        names.push(person.surname);
        if (person.children) {
            person.children.forEach(child =>
                DataAnalysis.populateNames(child, names)
            );
        }
    }

    static extractOriginData(data) {
        const origins = [];
        DataAnalysis.populateOrigins(data, origins);

        const originFrequency = DataAnalysis.calcFrequency(origins);
        return DataAnalysis.createDataTable(originFrequency);
    }

    static populateOrigins(person, origins) {
        if (person.origin) {
            origins.push(person.origin);
        }
        if (person.children) {
            person.children.forEach(child =>
                DataAnalysis.populateOrigins(child, origins)
            );
        }
    }
}
