import { dataLoaded } from './Types';
import datasource from '../../datasource/Datasource';
import { populateSearchOptions } from './SearchActions';

export const loadData = () => dispatch => {
    datasource.loadData((people, facts, places) => {
        dispatch(dataLoaded(people, facts, places));
        dispatch(dataReady());
    });
};

export const dataReady = () => dispatch => {
    dispatch(populateSearchOptions());
};
