import XLSX from 'xlsx';
import Comparitor from './Comparitor';

const toBinaryString = response => {
    let data = new Uint8Array(response);
    let arr = [];
    for (let i = 0; i !== data.length; ++i) {
        arr[i] = String.fromCharCode(data[i]);
    }

    return arr.join('');
};

const updatePeopleIds = people => {
    for (const id in people) {
        people[id].id = parseInt(id) + 2;
    }
};

class Datasource {
    findPerson(state, id) {
        if (!id) {
            return null;
        }
        return state.dataRepository.people[id - 2];
    }

    findPeople(state) {
        return state.dataRepository.people
            .slice(0)
            .sort(new Comparitor('name').compare);
    }

    findFacts(state, id) {
        if (!id) {
            return null;
        }

        let result = [];
        for (const fact of state.dataRepository.facts) {
            if (fact.person_id === id) {
                result.push(fact);
            }
        }

        result = result.sort(new Comparitor('year').compare);

        return result;
    }

    findPlace(state, place) {
        if (!place) {
            return null;
        }
        return state.dataRepository.places[place];
    }

    shareFather(person1, person2) {
        return (
            (person2.father && person2.father === person1.father) ||
            (!person1.father &&
                !person2.father &&
                person2.mother &&
                person2.mother === person1.mother)
        );
    }

    shareMother(person1, person2) {
        return (
            (person2.mother && person2.mother === person1.mother) ||
            (!person1.mother &&
                !person2.mother &&
                person2.father &&
                person2.father === person1.father)
        );
    }

    mapHalfByFather(halfByFather, person) {
        let grpByMother;
        for (let j = 0; j < halfByFather.length; j++) {
            if (halfByFather[j][0].mother === person.mother) {
                grpByMother = halfByFather[j];
            }
        }

        if (!grpByMother) {
            grpByMother = [];
            halfByFather.push(grpByMother);
        }

        grpByMother.push(person);
    }

    mapHalfByMother(halfByMother, person) {
        let grpByFather;
        for (let j = 0; j < halfByMother.length; j++) {
            if (halfByMother[j][0].father === person.father) {
                grpByFather = halfByMother[j];
            }
        }

        if (!grpByFather) {
            grpByFather = [];
            halfByMother.push(grpByFather);
        }
        grpByFather.push(person);
    }

    findSiblings(state, id) {
        let child = this.findPerson(state, id);

        let parents = {};
        let fullSiblings = [];
        let halfByFather = [];
        let halfByMother = [];

        for (let i = 0; i < state.dataRepository.people.length; i++) {
            let person = this.findPerson(state, i + 2);
            let shareFather = this.shareFather(child, person);
            let shareMother = this.shareMother(child, person);

            if (shareFather && shareMother) {
                fullSiblings.push(person);
            } else if (shareFather) {
                this.mapHalfByFather(halfByFather, person);
            } else if (shareMother) {
                this.mapHalfByMother(halfByMother, person);
            }

            if (shareFather || shareMother) {
                if (person.father && !parents[person.father]) {
                    parents[`parent_${person.father}`] = this.findPerson(
                        state,
                        person.father
                    );
                }
                if (person.mother && !parents[person.mother]) {
                    parents[`parent_${person.mother}`] = this.findPerson(
                        state,
                        person.mother
                    );
                }
            }
        }

        const sortByBirth = new Comparitor('yearOfBirth').compare;
        fullSiblings = fullSiblings.sort(sortByBirth);
        halfByFather = halfByFather.map(grp => grp.sort(sortByBirth));
        halfByMother = halfByMother.map(grp => grp.sort(sortByBirth));

        return {
            fullSiblings: fullSiblings,
            halfByFather: halfByFather,
            halfByMother: halfByMother,
            parents: parents
        };
    }

    loadData(onLoaded) {
        const dataFile = './FamilyTreeDB.xlsx';
        const sourceFile = new XMLHttpRequest();

        sourceFile.open('GET', dataFile, true);
        sourceFile.responseType = 'arraybuffer';
        sourceFile.onload = () => {
            const binaryString = toBinaryString(sourceFile.response);
            const workbook = XLSX.read(binaryString, { type: 'binary' });

            const worksheetPeople = workbook.Sheets['people'];
            const people = XLSX.utils.sheet_to_json(worksheetPeople, {
                raw: true
            });

            updatePeopleIds(people);

            const worksheetFacts = workbook.Sheets['facts'];
            const facts = XLSX.utils.sheet_to_json(worksheetFacts, {
                raw: true
            });

            const worksheetPlaces = workbook.Sheets['places'];
            const places = this.indexedPlaces(
                XLSX.utils.sheet_to_json(worksheetPlaces, {
                    raw: true
                })
            );

            if (onLoaded) {
                onLoaded(people, facts, places);
            }
        };
        sourceFile.send();
    }

    indexedPlaces(placesArray) {
        const places = [];
        for (const index in placesArray) {
            places[placesArray[index].place] = placesArray[index];
        }
        return places;
    }
}

export default new Datasource();
