import * as d3 from 'd3';
import DataAnalysis from './DataAnalysis';

export default class NameFrequency {
    constructor(anchor, colourMap, selectCallback) {
        this.anchor = anchor;
        this.colourMap = colourMap;
        this.selectCallback = selectCallback;
        this.margin = { top: 20, right: 30, bottom: 40, left: 90 };
        this.width = 500 - this.margin.left - this.margin.right;
    }

    drawNameChart(data) {
        this.data = data;
        this.nameData = DataAnalysis.extractNameData(data);
        this.namesHeight = 15 * this.nameData.length;

        this.addNamesGraphics();
        this.calcXScale();
        this.calcYScale();

        this.addAxisGraphics();
        this.drawEntryBars();
    }

    calcXScale() {
        this.xScale = d3
            .scaleLinear()
            .domain([0, this.nameData[0].quantity])
            .range([0, this.width]);
    }

    calcYScale() {
        this.yScale = d3
            .scaleBand()
            .range([0, this.namesHeight])
            .domain(
                this.nameData.map(function(d) {
                    return d.name;
                })
            )
            .padding(0.1);
    }

    drawEntryBars() {
        this.drawBars(
            this.svg
                .selectAll('rect')
                .data(this.nameData)
                .enter()
                .append('rect')
                .on('click', (event, d) => {
                    this.selectName(d.name);
                })
                .style('cursor', 'pointer')
        )
            .append('svg:title')
            .text(this.popup);
    }

    selectName(name) {
        this.surname = this.surname && this.surname === name ? undefined : name;
        this.selectCallback(this.surname);
        this.redrawChart(this.data);
    }

    popup(d) {
        return `${d.name} (${d.quantity})`;
    }

    redrawNameChart(data) {
        this.surname = undefined;
        this.redrawChart(data);
    }

    redrawChart(data) {
        this.data = data ? data : this.data;
        const newData = DataAnalysis.extractNameData(this.data);
        this.hideAdditionalBars(newData.length);

        this.nameData = newData;
        this.namesHeight = 15 * this.nameData.length;

        this.calcXScale();
        this.calcYScale();

        this.drawBars(
            this.svg
                .selectAll('rect')
                .data(this.nameData)
                .merge(this.svg)
                .transition()
                .duration(750)
        )
            .select('title')
            .text(this.popup);

        this.addAxisGraphics();
    }

    hideAdditionalBars(newDataSize) {
        this.drawBars(
            this.svg
                .selectAll('rect')
                .data(this.nameData)
                .merge(this.svg),
            (d, i) => {
                if (i < newDataSize) {
                    return this.xScale(d.quantity);
                }
                return this.xScale(0);
            }
        );
        this.xAxis.remove();
        this.yAxis.remove();
    }

    drawBars(svgBase, xScaleOverride) {
        const x =
            xScaleOverride ||
            (d => {
                return this.xScale(d.quantity);
            });
        const y = this.yScale;

        return svgBase
            .attr('x', x(0))
            .attr('y', function(d) {
                return y(d.name);
            })
            .attr('width', function(d, i) {
                return x(d, i);
            })
            .attr('height', y.bandwidth())
            .attr('fill', d => {
                return this.colourMap(d.name);
            })
            .attr('fill-opacity', d =>
                !this.surname || this.surname === d.name ? 0.7 : 0.2
            );
    }

    addAxisGraphics() {
        this.xAxis = this.svg
            .append('g')
            .attr('transform', `translate(0,${this.namesHeight})`);
        const tickNumber = Math.min(this.nameData[0].quantity, 5);
        this.xAxis
            .call(
                d3
                    .axisBottom(this.xScale)
                    .ticks(tickNumber)
                    .tickFormat(d3.format(',d'))
            )
            .selectAll('text')
            .attr('transform', 'translate(-10,0)rotate(-45)')
            .style('text-anchor', 'end');
        this.yAxis = this.svg.append('g').call(d3.axisLeft(this.yScale));
    }

    addNamesGraphics() {
        this.svg = d3
            .select(this.anchor)
            .append('svg')
            .attr('width', this.width + this.margin.left + this.margin.right)
            .attr(
                'height',
                this.namesHeight + this.margin.top + this.margin.bottom
            )
            .append('g')
            .attr(
                'transform',
                `translate(${this.margin.left},${this.margin.top})`
            );
    }
}
