import React from 'react';
import Heading from './Heading';
import { connect } from 'react-redux';
import { loadData } from '../state/actions/DataRepositoryActions';
import PropTypes from 'prop-types';
import AnalysisView from './AnalysisView';
import TreeView from './TreeView';

export class FamilyTree extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            headerHeight: 0
        };
        this.headerElement = React.createRef();
        this.resizeHandler = this.resizeHandler.bind(this);
    }

    resizeHandler() {
        const headerHeight = this.headerElement.clientHeight;
        if (this.state.headerHeight !== headerHeight) {
            this.setState({ headerHeight });
        }
    }

    componentDidUpdate() {
        this.resizeHandler();
    }

    componentDidMount() {
        this.resizeHandler();
        window.addEventListener('resize', this.resizeHandler);
        this.props.loadData();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.resizeHandler);
    }

    render() {
        return (
            <div>
                <div
                    className="headerWrapper"
                    ref={ref => (this.headerElement = ref)}
                >
                    <Heading />
                </div>
                <div
                    className="viewPort"
                    style={{ top: this.state.headerHeight }}
                >
                    {this.props.dataLoaded && (
                        <span>
                            {this.props.view === 'ANALYSIS' && <AnalysisView />}
                            {this.props.view === 'TREE' && <TreeView />}
                        </span>
                    )}
                </div>
                <div className="footer">
                    <strong>
                        <a
                            href="https://cmatts.co.uk"
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            &copy; {new Date().getFullYear()} Calvin Matthews
                        </a>
                    </strong>
                </div>
            </div>
        );
    }
}

FamilyTree.propTypes = {
    dataLoaded: PropTypes.bool.isRequired,
    loadData: PropTypes.func.isRequired,
    view: PropTypes.string.isRequired
};

const mapStateToProps = state => ({
    dataLoaded: state.dataRepository.dataLoaded,
    rootId: state.tree.rootId,
    view: state.tree.view
});

export default connect(mapStateToProps, { loadData })(FamilyTree);
