import React from 'react';
import {
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    UncontrolledDropdown,
    Nav,
    Navbar,
    NavbarBrand
} from 'reactstrap';
import Search from './Search';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { changeRoot, changeView } from '../state/actions/TreeActions';
import { showPerson } from '../state/actions/DetailsActions';

export class Heading extends React.Component {
    changeRoot = () => {
        const defaultRootId = 2;
        this.props.showPerson();
        this.props.changeRoot(defaultRootId);
    };

    showTree = () => {
        this.props.changeView('TREE');
    };

    showAnalysis = () => {
        this.props.changeView('ANALYSIS');
    };

    render() {
        return (
            <div>
                <Navbar className="heading">
                    <NavbarBrand onClick={this.changeRoot}>
                        <img
                            className="titleImage"
                            src="./images/title.gif"
                            alt="Home"
                        />
                    </NavbarBrand>

                    <span className="ml-auto">
                        <Search />
                    </span>
                    <Nav navbar>
                        <UncontrolledDropdown nav>
                            <DropdownToggle nav>
                                <img
                                    src="./images/menu.svg"
                                    height={40}
                                    width={40}
                                    alt="Options"
                                />
                            </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem
                                    onClick={this.showTree}
                                    className="showTree"
                                >
                                    <img
                                        src="./images/tree.svg"
                                        height={40}
                                        width={40}
                                        alt="Tree View"
                                    />
                                    Tree View
                                </DropdownItem>
                                <DropdownItem
                                    onClick={this.showAnalysis}
                                    className="showAnalysis"
                                >
                                    <img
                                        src="./images/analysis.svg"
                                        height={40}
                                        width={40}
                                        alt="Analysis View"
                                    />
                                    Analysis View
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </Nav>
                </Navbar>
            </div>
        );
    }
}

Heading.propTypes = {
    changeView: PropTypes.func.isRequired,
    changeRoot: PropTypes.func.isRequired,
    showPerson: PropTypes.func.isRequired
};

export default connect(null, { changeView, changeRoot, showPerson })(Heading);
