import { DATA_LOADED } from '../actions/Types';

const initialState = {
    dataLoaded: false,
    people: [],
    facts: [],
    places: {}
};

export default function(state = initialState, action) {
    switch (action.type) {
        case DATA_LOADED:
            return {
                ...state,
                dataLoaded: true,
                people: action.data.people,
                facts: action.data.facts,
                places: action.data.places
            };
        default:
            return state;
    }
}
