import * as d3 from 'd3';
import * as topojson from 'topojson';
import uk from './uk.json';
import DataAnalysis from './DataAnalysis';

export default class OriginMap {
    constructor(anchor, places, selectCallback) {
        this.anchor = anchor;
        this.places = places;
        this.selectCallback = selectCallback;
        this.width = 910;
        this.height = 1020;
    }

    drawOriginChart(data) {
        this.projection = this.mapProjection();
        this.svg = this.addMapGraphics();
        this.path = d3.geoPath().projection(this.projection);

        this.drawRegions();
        this.drawBoundaries();

        this.drawPlaceNames();

        this.originData = DataAnalysis.extractOriginData(data)
            .filter(d => this.places[d.name])
            .sort((a, b) => d3.descending(a.quantity, b.quantity));

        this.drawOrigins();
    }

    redrawOriginChart(data) {
        this.place = undefined;
        if (data) {
            const newOriginData = DataAnalysis.extractOriginData(data);
            for (const index in this.originData) {
                const newOriginDataItem = newOriginData.filter(
                    d => d.name === this.originData[index].name
                );
                this.originData[index].quantity =
                    newOriginDataItem.length > 0
                        ? newOriginDataItem[0].quantity
                        : 0;
            }
        }
        this.redrawChart();
    }

    redrawChart() {
        const radius = d3.scaleSqrt(
            [0, d3.max(this.originData, d => d.quantity)],
            [0, 40]
        );
        this.svg
            .selectAll('circle')
            .data(this.originData)
            .merge(this.svg)
            .transition()
            .duration(750)
            .attr('r', d => radius(d.quantity))
            .attr('fill-opacity', d =>
                !this.place || this.place === d.name ? 0.5 : 0.1
            )
            .select('title')
            .text(d => `${d.name} (${d.quantity})`);
    }

    drawOrigins(svg) {
        this.svg
            .append('g')
            .attr('fill', 'red')
            .attr('stroke', '#fff')
            .attr('stroke-width', 0.5)
            .selectAll('circle')
            .data(this.originData)
            .join('circle')
            .on('click', (event, d) => {
                this.selectPlace(d.name);
            })
            .attr(
                'transform',
                d =>
                    `translate(${this.projection([
                        this.places[d.name].long,
                        this.places[d.name].lat
                    ])})`
            )
            .append('title');
        this.redrawChart();
    }

    mapProjection() {
        return d3
            .geoAlbers()
            .center([0, 55.4])
            .rotate([4.4, 0])
            .parallels([50, 60])
            .scale(1200 * 5)
            .translate([this.width / 2 - 20, this.height / 2 - 80]);
    }

    addMapGraphics() {
        return d3
            .select(this.anchor)
            .append('svg')
            .attr('width', this.width)
            .attr('height', this.height);
    }

    drawRegions() {
        const regionData = topojson.feature(uk, uk.objects.subunits);

        const regionColour = {
            SCT: '#abf',
            WLS: '#eaa',
            NIR: '#aea',
            ENG: '#eee',
            IRL: '#ada'
        };

        this.svg
            .selectAll('.subunit')
            .data(regionData.features)
            .enter()
            .append('path')
            .attr('class', d => {
                return 'subunit ' + d.id;
            })
            .attr('fill', d => {
                return regionColour[d.id];
            })
            .attr('d', this.path);

        this.drawRegionNames(regionData);
    }

    drawRegionNames(regionData) {
        this.svg
            .selectAll('.subunit-label')
            .data(regionData.features)
            .enter()
            .append('text')
            .attr('transform', d => {
                return 'translate(' + this.path.centroid(d) + ')';
            })
            .attr('fill', '#888')
            .attr('fill-opacity', '.5')
            .attr('font-size', '20px')
            .attr('font-weight', '300')
            .attr('text-anchor', 'middle')
            .text(d => {
                return d.properties.name;
            });
    }

    drawPlaceNames() {
        const places = topojson.feature(uk, uk.objects.places);
        const plotted = {};

        this.svg
            .append('path')
            .datum(places)
            .attr('d', this.path)
            .style('fill', '#777');

        this.svg
            .selectAll('.label')
            .data(
                places.features.sort((a, b) =>
                    d3.descending(
                        a.geometry.coordinates[0],
                        b.geometry.coordinates[0]
                    )
                )
            )
            .enter()
            .append('text')
            .attr('clashDetection', d => {
                d.clash = this.detectLabelClash(plotted, d);
                plotted[d.properties.name] = d.geometry.coordinates;
                return d.clash;
            })
            .attr('transform', d => {
                return (
                    'translate(' + this.projection(d.geometry.coordinates) + ')'
                );
            })
            .attr('x', d => {
                return d.clash ? -6 : 6;
            })
            .attr('dy', '.35em')
            .style('text-anchor', d => {
                return d.clash ? 'end' : 'start';
            })
            .style('font-size', '.75rem')
            .style('fill', '#999')
            .text(d => {
                return d.properties.name;
            });
    }

    detectLabelClash(plotted, d) {
        for (const plot in plotted) {
            if (
                Math.abs(d.geometry.coordinates[0] - plotted[plot][0]) < 1 &&
                d.geometry.coordinates[0] - plotted[plot][0] < 0 &&
                Math.abs(d.geometry.coordinates[1] - plotted[plot][1]) < 0.1
            ) {
                return true;
            }
        }
        return false;
    }

    drawBoundaries() {
        this.svg
            .append('path')
            .datum(
                topojson.mesh(uk, uk.objects.subunits, (a, b) => {
                    return a !== b;
                })
            )
            .attr('d', this.path)
            .attr('fill', 'none')
            .attr('stroke', '#777')
            .attr('stroke-dasharray', '2,2')
            .attr('stroke-linejoin', 'round');
    }

    selectPlace(name) {
        this.place = this.place && this.place === name ? undefined : name;
        this.selectCallback(this.place);
        this.redrawChart();
    }
}
