import { SET_ROOT, SET_VIEW } from '../actions/Types';

const initialState = {
    rootId: 2,
    view: 'TREE'
};

export default function(state = initialState, action) {
    switch (action.type) {
        case SET_ROOT:
            return {
                ...state,
                rootId: action.data.rootId,
                view: initialState.view
            };
        case SET_VIEW:
            return {
                ...state,
                view: action.data.view
            };
        default:
            return state;
    }
}
