import React from 'react';
import {
    Col,
    Container,
    Row,
    UncontrolledPopover,
    PopoverBody
} from 'reactstrap';
import Person from './Person';
import datasource from '../datasource/Datasource';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

export class Siblings extends React.Component {
    renderPerson = person => {
        return <Person person={person} />;
    };

    renderHalfByFather = siblings => {
        return siblings.halfByFather.map((group, index) => (
            <Row className="halfByFather" key={index}>
                <Col xs={6} className="childrenByFather">
                    <Container fluid={true}>
                        {this.renderChildren(group)}
                    </Container>
                </Col>
                <Col xs={6} className="stepMother">
                    <Container fluid={true}>
                        {this.renderChildren([
                            siblings.parents[`parent_${group[0].mother}`]
                        ])}
                    </Container>
                </Col>
            </Row>
        ));
    };

    renderHalfByMother = siblings => {
        return siblings.halfByMother.map((group, index) => (
            <Row className="halfByMother" key={index}>
                <Col xs={6} className="childrenByMother">
                    <Container fluid={true}>
                        {this.renderChildren(group)}
                    </Container>
                </Col>
                <Col xs={6} className="stepFather">
                    <Container fluid={true}>
                        {this.renderChildren([
                            siblings.parents[`parent_${group[0].father}`]
                        ])}
                    </Container>
                </Col>
            </Row>
        ));
    };

    renderFullSiblings = siblings => {
        let group = siblings.fullSiblings;
        let father = siblings.parents[`parent_${group[0].father}`];
        let mother = siblings.parents[`parent_${group[0].mother}`];

        return (
            <Row className="fullSiblings">
                <Col xs={6} className="siblingChildren">
                    <Container fluid={true}>
                        {this.renderChildren(group)}
                    </Container>
                </Col>
                <Col xs={6} className="siblingParents">
                    <Container fluid={true}>
                        {this.renderChildren([father, mother])}
                    </Container>
                </Col>
            </Row>
        );
    };

    renderChildren = group => {
        return group.map((person, index) => (
            <Row key={index}>
                <Col
                    xs={12}
                    className={
                        person && person.id === this.props.personId
                            ? 'siblingPerson font-weight-bold'
                            : 'siblingPerson'
                    }
                >
                    {this.renderPerson(person)}
                </Col>
            </Row>
        ));
    };

    render() {
        let siblings = this.props.siblings;
        let enablePopover = Object.keys(siblings.parents).length > 0;
        return (
            <div className="siblings">
                {enablePopover && (
                    <UncontrolledPopover
                        placement="left"
                        trigger="hover"
                        delay={1000}
                        target={`personCol${this.props.personId}`}
                        className="siblingPopper"
                    >
                        <PopoverBody>
                            <Container fluid={true}>
                                {this.renderHalfByFather(siblings)}
                                {this.renderFullSiblings(siblings)}
                                {this.renderHalfByMother(siblings)}
                            </Container>
                        </PopoverBody>
                    </UncontrolledPopover>
                )}
            </div>
        );
    }
}

Siblings.propTypes = {
    personId: PropTypes.number.isRequired
};

const mapStateToProps = (state, ownProps) => ({
    siblings: datasource.findSiblings(state, ownProps.personId)
});

export default connect(mapStateToProps, {})(Siblings);
