export default class Comparitor {
    constructor(field) {
        this.field = field;
    }

    compare = (a, b) => {
        return b[this.field] === undefined || a[this.field] > b[this.field]
            ? 1
            : a[this.field] < b[this.field]
            ? -1
            : a[this.field] === undefined
            ? -1
            : 0;
    };
}
