import React from 'react';
import { personType } from '../datasource/DataTypes';
import PropTypes from 'prop-types';

export default class Person extends React.Component {
    renderLifespan = person => {
        let yearOfBirth = person.yearOfBirth ? person.yearOfBirth : '?';
        let yearOfDeath = person.yearOfDeath ? person.yearOfDeath : '?';
        return `(${yearOfBirth} - ${yearOfDeath})`;
    };

    render() {
        let person = this.props.person;

        let name = '?';
        let lifespan = '';

        if (person) {
            name = person.name;
            lifespan = this.renderLifespan(person);
        }

        return (
            <div id={this.props.divId}>
                <br />
                <div className="personName">{name}</div>
                <div className="personLifespan">{lifespan}</div>
                <br />
            </div>
        );
    }
}

Person.propTypes = {
    person: personType,
    divId: PropTypes.string
};
