import * as d3 from 'd3';

export default class AncestryCoverage {
    constructor(anchor, colourMap, rootChange) {
        this.anchor = anchor;
        this.colourMap = colourMap;
        this.rootChange = rootChange;
    }

    drawAncestorChart(data) {
        const root = this.partition(data);

        const width = 200;
        const radius = width / (2 * root.height + 1);

        this.drawArc = this.defineArcFn(radius);

        root.each(d => (d.current = d));
        this.svgSunburst = this.createSvg(width);
        this.ancestorGraphics = this.addAncestorGraphics(width);
        this.path = this.addPathGraphics(root);

        this.parent = this.addParentGraphic(root, radius);

        const zoomEvent = this.zoomFn(root);

        this.addPathPopup();
        this.addParentPopup();
        this.addParentClick(zoomEvent);
        this.addPathClick(zoomEvent);
    }

    zoomFn(root) {
        return (event, p) => {
            this.parent.datum(p.parent || root);

            root.each(
                d =>
                    (d.target = {
                        x0:
                            Math.max(
                                0,
                                Math.min(1, (d.x0 - p.x0) / (p.x1 - p.x0))
                            ) *
                            2 *
                            Math.PI,
                        x1:
                            Math.max(
                                0,
                                Math.min(1, (d.x1 - p.x0) / (p.x1 - p.x0))
                            ) *
                            2 *
                            Math.PI,
                        y0: Math.max(0, d.y0 - p.depth),
                        y1: Math.max(0, d.y1 - p.depth)
                    })
            );

            const transitionAni = this.ancestorGraphics
                .transition()
                .duration(750);
            this.path
                .transition(transitionAni)
                .tween('data', d => {
                    const i = d3.interpolate(d.current, d.target);
                    return t => (d.current = i(t));
                })
                .attrTween('d', d => () => this.drawArc(d.current))
                .attrTween('fill-opacity', () => () => 0.7);

            this.updateParentPopup(p);
            this.rootChange(p.data);
        };
    }

    addPathClick(clicked) {
        this.path
            .filter(d => d.children)
            .style('cursor', 'pointer')
            .on('click', clicked);
    }

    selectName(name) {
        this.path
            .transition(this.ancestorGraphics.transition())
            .attrTween('fill-opacity', d => () =>
                !name || name === d.data.surname ? 0.7 : 0.2
            );
    }

    selectPlace(place) {
        this.path
            .transition(this.ancestorGraphics.transition())
            .attrTween('fill-opacity', d => () =>
                !place || place === d.data.origin ? 0.7 : 0.2
            );
    }

    addParentClick(clicked) {
        this.parent.style('cursor', 'pointer').on('click', clicked);
    }

    addParentGraphic(root, radius) {
        return this.ancestorGraphics
            .append('circle')
            .datum(root)
            .attr('r', radius)
            .attr('fill', 'none')
            .attr('pointer-events', 'all');
    }

    addPathPopup() {
        this.path.append('title').text(this.renderPopup);
    }

    addParentPopup() {
        this.parent.append('title').text(this.renderPopup);
    }

    renderPopup = d => {
        let yearOfBirth = d.data.yearOfBirth ? d.data.yearOfBirth : '?';
        let yearOfDeath = d.data.yearOfDeath ? d.data.yearOfDeath : '?';
        return `${d.data.name}\n(${yearOfBirth} - ${yearOfDeath})`;
    };

    updateParentPopup(d) {
        this.parent.select('title').text(() => {
            return this.renderPopup(d);
        });
    }

    addPathGraphics(root) {
        return this.ancestorGraphics
            .append('g')
            .selectAll('path')
            .data(root)
            .join('path')
            .attr('fill', d => {
                return this.colourMap(d.data.surname);
            })
            .attr('fill-opacity', () => 0.7)
            .attr('d', d => this.drawArc(d.current));
    }

    addAncestorGraphics(width) {
        return this.svgSunburst
            .append('g')
            .attr('transform', `translate(${width / 2},${width / 2})`);
    }

    createSvg(width) {
        return d3
            .select(this.anchor)
            .append('svg')
            .attr('viewBox', [0, 0, width, width])
            .style('font', '10px sans-serif');
    }

    bisect(maxHeight) {
        return node => {
            node.value = Math.pow(2, maxHeight - node.depth);
        };
    }

    padSingleMother = node => {
        if (
            node.parent &&
            node.parent.children.length === 1 &&
            node.data.mother
        ) {
            const size = node.x1 - node.x0;
            this.shiftNodes(size)(node);
        }
    };

    shiftNodes(arc) {
        return node => {
            node.x0 = node.x0 + arc;
            node.x1 = node.x1 + arc;
            if (node.children) {
                node.children.forEach(this.shiftNodes(arc));
            }
        };
    }

    partition(data) {
        const root = d3.hierarchy(data);
        root.each(this.bisect(root.height));

        const partition = d3.partition().size([2 * Math.PI, root.height + 1])(
            root
        );

        root.each(this.padSingleMother);
        return partition;
    }

    defineArcFn(radius) {
        return d3
            .arc()
            .startAngle(d => d.x0)
            .endAngle(d => d.x1)
            .padRadius(radius * 1.5)
            .innerRadius(d => d.y0 * radius)
            .outerRadius(d => Math.max(d.y0 * radius, d.y1 * radius - 1));
    }
}
